@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  --scrollbarBG: #00B4CC;
  --thumbBG: yellow;
}

.App {
  max-width: 800px;
  margin:30px auto;
}

.spinner {
  text-align: center;
  color: #00B4CC;
  font-size: 4rem;
}

.fa-spinner {
  animation: fa-spin 2s linear infinite
}

@keyframes fa-spin {
  0% {
      transform: rotate(0deg)
  }

  to {
      transform: rotate(1turn)
  }
}
.search {
  position: relative;
  display: flex;
  padding: 0 15px;
}
.search label {
  display: flex;
  flex:1 1;
  flex-direction: column;
}
.search label .warning{
  margin:0;
  color:red;
}
.searchTerm {
  border: 3px solid #00b4cc;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid #00b4cc;
  background: #00b4cc;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.results {
  max-height: 80vh;
  overflow-y: auto;
  margin: 10px auto;
  padding: 10px;
}

.results::-webkit-scrollbar {
  width: 11px;
}
.results {
  scrollbar-width: thin;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}
.results::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
}
.results::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  border-radius: 6px;
  border: 3px solid var(--scrollbarBG);
}
.error {
  text-align: center;
  font-size: 20px;
  color: red;
}
.error .far {
  margin-right: 8px;
}

.results ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.results ul li {
  padding: 0;
}
.results ul li a {
  text-decoration: none;
  padding: 10px;
  display: block;
}
.results ul li a:hover {
  background-color: var(--scrollbarBG);
}
.results ul li a:hover article {
  color: white;
}
.results ul li a article {
  display: flex;
  flex-direction: row;
  color: var(--scrollbarBG);
}

.results ul li a article figure {
  padding: 0;
  max-width: 100px;
  margin: 0;
  margin-right: 10px;
  background-color: #eeeeee;
}
.results ul li a article figure img {
  max-width: 100%;
}

article.movie {
    background-color: #fafafa;
    border: 1px solid #e8e8e8;
    box-shadow: inset 0 1px 1px rgb(0 0 0 / 5%);
    padding: 10px;
    display: flex;
    flex-direction: row;
    min-height: 500px;
}

article.movie figure {
    padding:0;
    margin:0;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;

}
article.movie figure img {
    width: 300px;
}
